import React, { useContext, useEffect } from 'react';
import { ClipLoader } from 'react-spinners';
import useFetchHubSpotForms from '../../../hooks/useFetchHubSpotForms';
import { colors } from '../../../utils/colors';
import { AppContext } from '../../common/Layout/Layout';
import {
  ClipLoaderContainer,
  StyledTalkToAnExpertForm,
  TalkToAnExpertFormContainer,
} from './styled';
import { Container } from '../../common/Container/Container';
import { Text } from '../../common';

export const TalkToAnExpertForm = () => {
  const appContext = useContext(AppContext);

  useFetchHubSpotForms(
    [appContext.hubSpot.isHubSpotFormsLoaded],
    !appContext.hubSpot.isHubSpotFormsLoaded,
    appContext.hubSpot.setIsHubSpotFormsLoaded,
  );

  useEffect(() => {
    //@ts-ignore
    window.hbspt?.forms.create({
      region: 'na1',
      portalId: '21630623',
      formId: '13eb957c-10fc-412f-addf-ba9267bd7672',
      target: '#talkToAnExpertForm',
      onFormSubmit: function () {
        //@ts-ignore
        if (window.enabledEventSettings) {
          //@ts-ignore
          window.enabledEventSettings.FACEBOOK = [];
        }
        //@ts-ignore
        gtag_report_conversion();
      },
      onFormReady: function (form: Element) {
        const gclidField = form.querySelector('input[name="gclid_field"]');
        if (gclidField) {
          gclidField.id = 'gclid_field';
        }
      },
    });
  }, [appContext.hubSpot.isHubSpotFormsLoaded]);

  return (
    <TalkToAnExpertFormContainer id="talk-to-an-expert">
      <Container>
        <Text.Title.Bold.lg>Talk to an expert.</Text.Title.Bold.lg>

        {appContext.hubSpot.isHubSpotFormsLoaded ? (
          <StyledTalkToAnExpertForm id="talkToAnExpertForm"></StyledTalkToAnExpertForm>
        ) : (
          <ClipLoaderContainer>
            <ClipLoader color={colors.primary.lightOrange} />
          </ClipLoaderContainer>
        )}
      </Container>
    </TalkToAnExpertFormContainer>
  );
};
