import React, { FC, PropsWithChildren, ReactElement } from 'react';
import { InternalLinks } from '../../utils/links';
import { Table, Text } from '../common';
import { PrimaryButton } from '../common/Buttons/Buttons';
import { Container } from '../common/Container/Container';
import { StaticImage } from 'gatsby-plugin-image';
import { Link } from 'gatsby';
import {
  CtaBlockContent,
  CtaBlockImageWrapper,
  FeatureComparisonTableContainer,
  HeroFeaturesList,
  HeroParagraph,
  HeroWrapper,
  ImageWrapper,
  LinksWrapper,
  QuoteIconsContainer,
  StyledCtaBlock,
  StyledFeatureComparisonSection,
  StyledHeroInfo,
  StyledHeroSection,
  StyledQuotesSection,
  StyledQuoute,
} from './styled';
import { colors } from '../../utils/colors';
import { FeatureBlock } from '../common/FeatureBlock';
import SuccessIcon from '../common/Icons/SuccessIcon/SuccessIcon';
import Slider from 'react-slick';
import SingleQuoteIcon from '../common/Icons/SingleQuoteIcon';
import DesktopLogoImage from '../../images/Logo/Telgorithm/DesktopLogo.svg';

type Quote = {
  text: string;
  author: string;
};

type CtaBlockProps = PropsWithChildren<{
  image: ReactElement;
  title: string;
  largeTitle?: boolean;
  imagePosition: 'left' | 'right';
}>;

type HeroSectionProps = PropsWithChildren<{ title: string; advantages: string[] }>;

type FeatureComparisonSectionProps = PropsWithChildren<{
  title: string;
  competitorLogoSrc: string;
}>;

type QuotesSectionProps = { quotes: Quote[] };

export const HeroSection: FC<HeroSectionProps> = ({ title, children, advantages }) => {
  return (
    <StyledHeroSection>
      <Container>
        <StyledHeroInfo>
          <HeroWrapper>
            <Text.Title.Bold.xl>{title}</Text.Title.Bold.xl>
            <HeroParagraph>
              <HeroFeaturesList>
                {advantages.map((advantage) => (
                  <FeatureBlock
                    icon={<SuccessIcon color={colors.primary.lightOrange} />}
                    title=""
                    key={advantage}
                  >
                    <Text.Regular.Bold.md>{advantage}</Text.Regular.Bold.md>
                  </FeatureBlock>
                ))}
              </HeroFeaturesList>
            </HeroParagraph>
            <LinksWrapper>
              <div>
                <Link to="#talk-to-an-expert">
                  <PrimaryButton>Talk to an Expert</PrimaryButton>
                </Link>
              </div>
            </LinksWrapper>
          </HeroWrapper>
          <ImageWrapper>{children}</ImageWrapper>
        </StyledHeroInfo>
      </Container>
    </StyledHeroSection>
  );
};

export const CtaBlock: FC<CtaBlockProps> = ({
  title,
  largeTitle,
  image,
  children,
  imagePosition,
}) => {
  return (
    <StyledCtaBlock imagePosition={imagePosition}>
      <CtaBlockImageWrapper>{image}</CtaBlockImageWrapper>
      <CtaBlockContent>
        {largeTitle ? (
          <Text.Title.Bold.lg>{title}</Text.Title.Bold.lg>
        ) : (
          <Text.Title.Bold.md>{title}</Text.Title.Bold.md>
        )}
        <div>{children}</div>
      </CtaBlockContent>
    </StyledCtaBlock>
  );
};

export const QuotesSection: FC<QuotesSectionProps> = ({ quotes }) => {
  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: false,
    arrows: quotes.length > 1,
  };

  return (
    <StyledQuotesSection>
      <Container>
        <Slider {...settings}>
          {quotes.map(({ author, text }) => (
            <div>
              <StyledQuoute>
                <QuoteIconsContainer>
                  <SingleQuoteIcon />
                  <SingleQuoteIcon />
                </QuoteIconsContainer>
                <p>
                  <Text.Regular.Normal.lg>{text}</Text.Regular.Normal.lg>
                </p>
                <p>
                  <Text.Regular.Bold.lg>{author}</Text.Regular.Bold.lg>
                </p>
              </StyledQuoute>
            </div>
          ))}
        </Slider>
      </Container>
    </StyledQuotesSection>
  );
};

export const FeatureComparisonSection: FC<FeatureComparisonSectionProps> = ({
  title,
  competitorLogoSrc,
  children,
}) => (
  <StyledFeatureComparisonSection>
    <div>
      <Text.Title.Bold.sm>FEATURE COMPARISON</Text.Title.Bold.sm>
      <Text.Title.Bold.lg>{title}</Text.Title.Bold.lg>
    </div>
    <FeatureComparisonTableContainer>
      <Table.Container>
        <Table.Wrapper>
          <Table>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Features</Table.HeaderCell>
                <Table.HeaderCell>
                  <img src={DesktopLogoImage} />
                </Table.HeaderCell>
                <Table.HeaderCell>
                  <img src={competitorLogoSrc} />
                </Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>{children}</Table.Body>
          </Table>
        </Table.Wrapper>
      </Table.Container>
    </FeatureComparisonTableContainer>
  </StyledFeatureComparisonSection>
);
