import styled from 'styled-components';
import heroSectionBackground from '../../images/Backgrounds/glass-lines-background.webp';
import { colors, opacityToHex } from '../../utils/colors';
import { device } from '../../utils/layoutBreakpoints';
import quotesSectionBackground from '../../images/Backgrounds/womanWithLaptop.png';
import {
  TableBody,
  TableBodyCell,
  TableHeaderCell,
  TableRow,
} from '../common/Table/styled';

export const StyledHeroSection = styled.section`
  background-image: url(${heroSectionBackground});
  background-color: ${colors.secondary.gray700 + opacityToHex['0.05']};
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 20px;
  overflow: hidden;
  @media ${device.laptop} {
    padding-bottom: 80px;
    padding-top: 64px;
  }
`;

export const StyledHeroInfo = styled.div`
  padding-top: 40px;
  font-family: 'Rubik';
  display: flex;
  flex-direction: column;
  gap: 30px;

  h1 {
    font-weight: 600;
    letter-spacing: -1px;
  }

  @media ${device.tablet} {
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
    padding-bottom: 0;
  }

  @media ${device.desktop} {
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
  }

  p {
    line-height: 1.2rem;
    font-weight: 500;
  }
`;

export const HeroWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 16px;
  align-items: center;
  text-align: center;

  & > p:first-child {
    font-size: 0.8rem;
    font-weight: 700;
    @media ${device.laptop} {
      font-size: 1.2rem;
    }
  }

  @media ${device.tablet} {
    align-items: flex-start;
    text-align: left;
  }

  @media ${device.laptop} {
    margin-left: 0;
    margin-bottom: 40px;
    gap: 30px;
    align-items: flex-start;
  }
`;

export const LinksWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
`;

export const HeroParagraph = styled.p`
  max-width: 540px;
`;

export const ImageWrapper = styled.div`
  display: flex;
  align-items: center;
  .heroImage {
    position: relative;
    overflow: visible;
  }

  @media ${device.mobileXL} {
    max-width: 70%;
    margin: 0 auto;
  }

  @media ${device.tablet} {
    max-width: 40%;
    margin: unset;
  }

  @media ${device.laptop} {
    max-width: 50%;
  }

  @media ${device.desktop} {
    max-width: 45%;
  }
`;

export const HeroFeaturesList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: left;
`;

export const StyledCtaBlock = styled.div<{ imagePosition: 'left' | 'right' }>`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;

  @media ${device.tablet} {
    flex-direction: ${(props) =>
      props.imagePosition === 'left' ? 'row' : 'row-reverse'};
    gap: 50px;
    justify-content: space-between;
    align-items: center;
  }
`;

export const CtaBlockImageWrapper = styled.div`
  width: 85%;
  @media ${device.mobileXL} {
    width: 75%;
  }

  @media ${device.mobileXXL} {
    width: 60%;
  }

  @media ${device.tablet} {
    width: 45%;
  }
`;

export const CtaBlockContent = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  h2 {
    font-weight: 600;
  }
  @media ${device.tablet} {
    gap: 40px;
    width: 50%;
  }
`;

export const StyledQuotesSection = styled.section`
  padding: 150px 0;
  background: url(${quotesSectionBackground});
  background-repeat: no-repeat;
  background-size: cover;
  max-width: 2160px;
  margin: 0 auto;

  background-position: -550px;

  .slick-slider {
    padding: 0 25px;
  }

  @media ${device.mobileXL} {
    background-position: -350px;
  }

  @media ${device.tablet} {
    background-position: -200px;
  }

  @media ${device.laptop} {
    background-position: -100px;
    margin-left: calc(auto + 100px);
  }

  .slick-prev:before,
  .slick-next:before {
    font-size: 30px;
    color: ${colors.primary.gray500};
  }

  .slick-prev {
    left: -10px;
  }

  .slick-next {
    right: -10px;
  }

  .slick-arrow {
    width: 35px;
    height: 35px;
  }

  @media ${device.laptop} {
    .slick-slider {
      padding: 0 25px;
    }

    .slick-prev {
      left: 10px;
    }

    .slick-next {
      right: 10px;
    }
  }
`;

export const StyledQuoute = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: center;
  align-items: center;

  span {
    color: ${colors.primary.white};
    line-height: 20px;
 
    @media ${device.tablet} {
      line-height: 30px;
    }

    @media ${device.laptop} {
      line-height: 40px;
    }
  }

  svg {
    path {
      fill: ${colors.primary.white};
    }
  }

  @media ${device.tablet} {
    p {
      width: 70%;
    }
  }
`;

export const QuoteIconsContainer = styled.div`
  display: flex;
  gap: 5px;
`;

export const StyledFeatureComparisonSection = styled.section`
  display: flex;
  flex-direction: column;
  gap: 40px;

  h2 {
    font-weight: 600;
  }

  & > div:first-child {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }
`;

export const FeatureComparisonTableContainer = styled.div`
  ${TableHeaderCell} {
    padding: 40px 0 40px 24px;
    img {
      max-height: 22px;
    }
  }
  ${TableBody} {
    ${TableRow} {
      &:nth-child(2n + 1) {
        background-color: #f9f9f9;
      }
      ${TableBodyCell} {
        padding: 40px 0 40px 24px;
        line-height: 30px;
        a {
          text-decoration: underline;
          font-weight: 400;

          &:hover {
            color: ${colors.secondary.orange};
          }
        }
      }

      ${TableBodyCell}:first-child {
        background: linear-gradient(90deg, #ff982b 0%, #ff4d49 100%);
        background-clip: text;
        background-size: 100%;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-family: 'Poppins';
        vertical-align: baseline;
        width: 20%;
      }

      ${TableBodyCell}:not(:first-child) {
        font-weight: 300;
        width: 40%;
        vertical-align: baseline;
      }
    }
  }
`;

export const CtaBlockBody = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;

  @media ${device.laptop} {
    gap: 30px;
  }

  a {
    color: ${colors.primary.blue};
    transition: 0.3s;
    &:hover {
      color: ${colors.primary.lightOrange};
    }
  }
`;

export const CalculatorContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;

  h2 {
    img {
      display: inline-block;
      position: relative;
      top: 5px;
      margin: 0 5px;

      @media ${device.tablet} {
        margin: 0 15px;
      }

      @media ${device.laptop} {
        height: 40px;
      }
    }
  }

  h2 {
    text-align: center;
    width: 100%;
    margin: 0 auto;
    font-weight: 600;
  }

  @media ${device.laptop} {
    gap: 30px;
    h2 {
      width: 75%;
    }
  }
`;
