import styled from 'styled-components';
import { colors } from '../../../utils/colors';
import { device } from '../../../utils/layoutBreakpoints';

export const TalkToAnExpertFormContainer = styled.div`
  display: flex;
  background-color: ${colors.secondary.gray100};
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 50px 0;

  h2 {
    margin-bottom: 30px;
    font-weight: 600;
  }

  @media ${device.laptop} {
    padding: 100px 0;
  }
`;

export const StyledTalkToAnExpertForm = styled.div`
  font-size: 0.8rem;
  line-height: 1.1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  width: 100%;
  min-height: 400px;
`;

export const ClipLoaderContainer = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  
  min-height: 400px;
`;
